@import "../../global.scss";

.Contact{
    background-color: white;
    display: flex;
    @include mobile{
    flex-direction: column;
    overflow: hidden;
    }
    .left{
        flex: 1;
        overflow: hidden;

        img{
            height: 100%;
            overflow: hidden;
        }
    }
    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        h2{
            font-size: 40px;

        }
        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input{
                width: 300px;
                height: 30px;
                font-size: 13px;
                border-radius: 10px;
                @include mobile{
                    width: 200px;
                    height: 20px;
                }
            }
            textarea{
                width: 300px;
                height: 200px;
                font-size: 13px;
                border-radius: 10px;
                @include mobile{
                    width: 200px;
                    height: 100px;
                }

            }
            button{
                width: 150px;
                height: 30px;
                color: whitesmoke;
                background-color: darkgray;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;
                &:focus{
                    outline: none;
                }

            }
            span{
                color: green;
            }
        }
    }
}